import React, { useContext, useState, useEffect } from "react"
import { useHistory } from "react-router-dom"
import { auth, db } from "../firebase"

const AuthContext = React.createContext()

export const useAuth = () => {
    return useContext(AuthContext)
}

export const AuthProvider = ({ children }) => {
    const [currentUser, setCurrentUser] = useState();
    const [userCompanies, setUserCompanies] = useState([]);
    const [clients, setClients] = useState([])
    const [invoices, setInvoices] = useState([])
    const [invoicesEarnings, setInvoicesEarnings] = useState();
    const [lastInvoiceData, setLastInvoiceData] = useState({});
    const [products, setProducts] = useState([])
    const [loading, setLoading] = useState(true);
    const [dbLoading, setDbLoading] = useState(false);
    const [error, setError] = useState('');

    const history = useHistory();

    const signup = async (name, email, phone, password) => {
        setError('')
        setDbLoading(true)
        try{
            await auth.createUserWithEmailAndPassword(email, password)
            .then(res => {
                const user = res.user;

                user.updateProfile({
                    displayName: name
                })

                //user.updatePhoneNumber(phone)

                user.sendEmailVerification();
                history.push('/')

            })
        }
        catch(err){
            setError(err.message)
        }
        setDbLoading(false)
    }

    const verifyEmail = () => {
        try{
            auth.onAuthStateChanged(user => user.sendEmailVerification())
            alert(`Email verification sent.\nRefresh the page after you verify your email.`)
        }
        catch(err){
            console.log(err.message)
        }
    }

    const login = async (email, password) => {
        setError('')
        setDbLoading(true)
        try { 
            await auth.signInWithEmailAndPassword(email, password)
            history.push('/')
        }
        catch(err){
            setError(err.message)
        }
        setDbLoading(false)
    }

    const logout = () => {
        return auth.signOut()
    }

    const resetPassword = async (email) => {
        setDbLoading(true)
        try{
            await auth.sendPasswordResetEmail(email)
            return alert('Password reset link sent!')
        }
        catch(err){
            setError(err.message)
        }
        setDbLoading(false)
    }

    const updateEmail = (email) => {
        setError('')
        setDbLoading(true)
        try{
            currentUser.updateEmail(email)
        }
        catch(err){
            setError(err.message)
        }
        setDbLoading(false)
    }

    const updatePassword = (email) => {
        setError('')
        setDbLoading(true)
        try {
            currentUser.updatePassword(email)
        }
        catch (err) {
            setError(err.message)
        }
        setDbLoading(false)
    }

    const updateProfile = async (name, email, phone) => {
        try{
            currentUser.updateProfile({
                displayName: name
            })
            //currentUser.updatePhoneNumber(phone);
            updateEmail(email);
        }
        catch(err){
            console.log(err.message)
        }
    }

    //COMPANY 
    const getCompanies = async (id) => {
        try{
            setUserCompanies([]);
            await db.collection('Companies').where("uid", "==", id)
            .get().then(results => {
                results.forEach(res => {
                    const compData = {...res.data(), compId: res.id}
                    setUserCompanies(prev => [...prev, compData])
                })
            })
        }
        catch(err){
            console.log(err.message)
        }
    }

    const newCompany = async (name, cif, regCom, address, capSoc, bank, account, phone, tva, tvaPay, logo,
        serie, nrStart, term, delName, delCI, delCNP) => {
        try{
            await db.collection("Companies").add({
                uid: currentUser.uid,
                name,
                cif,
                regCom,
                address,
                capSoc,
                bank,
                account,
                phone,
                tva,
                tvaPay,
                logo,
                serie,
                nrStart,
                term,
                delName,
                delCI,
                delCNP
            });
            alert(`The company ${name} was added!`);
        }
        catch(err){
            console.log(err.message)
            //console.log('error creating company')
        }
    }

    const updateCompany = async (id, name, cif, regCom, address, capSoc, bank, account, phone, tva, tvaPay, logo,
        serie, nrStart, term, delName, delCI, delCNP) => {
        try{
            await db.collection('Companies').doc(id).update({
                name,
                cif,
                regCom,
                address,
                capSoc,
                bank,
                account,
                phone,
                tva,
                tvaPay,
                logo,
                serie,
                nrStart,
                term,
                delName,
                delCI,
                delCNP
            })
        }
        catch(err){
            console.log(err.message)
        }
    }

    const deleteCompany = async (id) => {
        const ask = window.confirm('Are you sure you want to delete this company?');
        if(ask){
            try{
                await db.collection("Companies").doc(id).delete()
            }
            catch(err){
                console.log(err.message)
            }
        }
    }

    //Clients
    const getClients = async (id) => {
        try {
            setClients([]);
            await db.collection('Clients').where("uid", "==", id)
                .get().then(results => {
                    results.forEach(res => {
                        const compData = { ...res.data(), cliId: res.id }
                        setClients(prev => [...prev, compData])
                    })
                })
        }
        catch (err) {
            console.log(err.message)
        }
    }

    const newClient = async (name, cif, regCom, address, bank, account) => {
        try {
            await db.collection("Clients").add({
                uid: currentUser.uid,
                name,
                cif,
                regCom,
                address,
                bank,
                account
            });
            alert(`The client ${name} was added!`);
        }
        catch (err) {
            console.log(err.message)
            //console.log('error creating company')
        }
    }

    const updateClient = async (id, name, cif, regCom, address, bank, account) => {
        try {
            await db.collection('Clients').doc(id).update({
                name,
                cif,
                regCom,
                address,
                bank,
                account
            })
        }
        catch (err) {
            console.log(err.message)
        }
    }

    const deleteClient = async (id) => {
        const ask = window.confirm('Are you sure you want to delete this client?');
        if (ask) {
            try {
                await db.collection("Clients").doc(id).delete()
            }
            catch (err) {
                console.log(err.message)
            }
        }
    }

    //get last number and series of company invoice
    const getLastInvoice = async (comp) => {
        try{
            await db.collection('Invoices').where("companyName", "==", comp)
            .orderBy('invoiceDate', "desc").limit(1)
            .get().then(results => {
                results.forEach( res => {
                    return setLastInvoiceData(res.data())
                })
            })
        }
        catch(err){
            console.log(err.message)
        }
    }

    //invoice
    const getInvoices = async (id) => {
        let sum = 0;
        try {
            setInvoices([]);
            await db.collection('Invoices').where("uid", "==", id).orderBy('invoiceDate', "desc")
            .get().then(results => {
                results.forEach(res => {
                    const compData = { ...res.data(), invId: res.id }
                    setInvoices(prev => [...prev, compData])
                    //sum total earnings
                    res.data().items.map(item => {
                        return sum += ((item.val && parseFloat(item.val)) + (item.tva && parseFloat(item.tva)));
                    })
                })
            })
            setInvoicesEarnings(sum);
        }
        catch (err) {
            console.log(err.message)
        }
    }

    const deleteInvoice = async (id) => {
        const ask = window.confirm('Are you sure you want to delete this invoice?');
        if (ask) {
            try {
                await db.collection("Invoices").doc(id).delete()
            }
            catch (err) {
                console.log(err.message)
            }
        }
    }

    const newInvoice = async (data) => {
        try {
            await db.collection("Invoices").add({
                uid: currentUser.uid,
                invoiceDate: data.invoiceDate,
                invoiceTerm: data.invoiceTerm,
                invoiceSeries: data.invoiceSeries,
                invoiceNr: data.invoiceNr,
                delName: data.delName,
                delCNP: data.delCNP,
                delSeries: data.delSeries,
                transport: data.transport,
                companyName: data.companyName,
                companyCIF: data.companyCIF,
                companyRegCom: data.companyRegCom,
                companyAdresa: data.companyAdresa,
                companyCapSocial: data.companyCapSocial,
                companyCont: data.companyCont,
                companyBank: data.companyBank,
                companyTelefon: data.companyTelefon,
                companyTVA: data.companyTVA,
                companyTVApay: data.companyTVApay,
                companyLogo: data.companyLogo,
                clientName: data.clientName,
                clientCIF: data.clientCIF,
                clientRegCom: data.clientRegCom,
                clientAdresa: data.clientAdresa,
                clientCont: data.clientCont,
                clientBank: data.clientBank,
                items: data.items
            });
        }
        catch (err) {
            console.log(err.message)
            //console.log('error creating company')
        }
    }

    //Products and services
    const getProducts = async(id) => {
        try {
            setProducts([]);
            await db.collection('Products').where("uid", "==", id)
                .get().then(results => {
                    results.forEach(res => {
                        const prodData = { ...res.data(), prodId: res.id }
                        setProducts(prev => [...prev, prodData])
                    })
                })
        }
        catch (err) {
            console.log(err.message)
        }
    }

    const deleteProduct = async (id) => {
        const ask = window.confirm('Are you sure you want to delete this product/service?');
        if (ask) {
            try {
                await db.collection("Products").doc(id).delete()
            }
            catch (err) {
                console.log(err.message)
            }
        }
    }

    const newProduct = async (name, um, price, compId) => {
        try {
            await db.collection("Products").add({
                uid: currentUser.uid,
                name,
                um,
                price,
                compId
            });
            alert(`The product/service ${name} was added!`);
        }
        catch (err) {
            console.log(err.message)
        }
    }

    const updateProduct = async (id, name, um, price, compId) => {
        try {
            await db.collection('Products').doc(id).update({
                name,
                um,
                price,
                compId
            })
        }
        catch (err) {
            console.log(err.message)
        }
    }

    //end
    useEffect(() => {
        const unsubscribe = auth.onAuthStateChanged(user => {
            setCurrentUser(user)
            setLoading(false)
        })
        return unsubscribe
    }, [])

    const value = {
        currentUser,
        login,
        signup,
        verifyEmail,
        logout,
        resetPassword,
        updateEmail,
        updatePassword,
        updateProfile,
        error,
        userCompanies,
        getCompanies,
        newCompany,
        updateCompany,
        deleteCompany,
        dbLoading,
        clients,
        getClients,
        newClient,
        updateClient,
        deleteClient,
        invoices,
        getInvoices,
        newInvoice,
        invoicesEarnings,
        getLastInvoice,
        lastInvoiceData,
        deleteInvoice,
        getProducts,
        products,
        deleteProduct,
        newProduct,
        updateProduct
    }

    return (
        <AuthContext.Provider value={value}>
            {!loading && children}
        </AuthContext.Provider>
    )
}