import { useState } from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import { AuthProvider } from "./Contexts/AuthContext";

import Invoice from "./Components/Invoice";
import MainMenu from "./Components/MainMenu";
import NewInvoice from "./Components/NewInvoice";
//import FrontPage from "./Components/FrontPage";
import Login from "./Components/Login";
import Register from "./Components/Register";
import PrivateRoute from "./Components/PrivateRoute";
import MyCompanies from "./Components/MyCompanies";
import MyProfile from "./Components/MyProfile";
import Clients from "./Components/Clients";
import History from "./Components/History";
import Products from "./Components/Products";

function App() {
  const [invoiceData, setInvoiceData] = useState([]);

  const generateInvoice = (data) => {
    setInvoiceData(data);
  }

  return (
    <div className="App">
      <Router>
        <AuthProvider>
          <Switch>
            <PrivateRoute exact path="/" component={MainMenu} />

            <PrivateRoute exact path="/react-invoice-generator-RO" component={MainMenu} />

            {/* <Route component={() => <FrontPage/>} path="/" exact /> */}

            {/* <Route component={() => <MainMenu/>} path="/menu" exact /> */}

            <Route component={Login} path="/login"  />

            <Route component={Register} path="/register"  />

            <PrivateRoute component={MyCompanies} path="/companies"  />

            <PrivateRoute component={MyProfile} path="/profile" />

            <PrivateRoute component={Clients} path="/clients" />

            <PrivateRoute component={Products} path="/products" />

            <PrivateRoute component={() => <History generateInvoice={generateInvoice}/>} path="/history" />

            <PrivateRoute component={() => <NewInvoice generateInvoice={generateInvoice}/>} path="/new-invoice" />

            <Route path="/generated-invoice">
              <Invoice data={invoiceData} />
            </Route>
          </Switch>
        </AuthProvider>
      </Router>
    </div>
  );
}

export default App;
