import { useState, useEffect } from "react";
import { useAuth } from "../Contexts/AuthContext";
import NavigationBar from "./NavigationBar";

import { useHistory } from "react-router-dom";

const Clients = () => {

    const history = useHistory();

    const { clients,
        newClient,
        getClients,
        currentUser,
        updateClient,
        deleteClient
    } = useAuth();

    const [addNew, setAddNew] = useState(false)
    const [update, setUpdate] = useState(false)

    //client company form
    const [companyId, setCompanyId] = useState('')
    const [companyName, setCompanyName] = useState('');
    const [companyCIF, setCompanyCIF] = useState('');
    const [companyRegCom, setCompanyRegCom] = useState('');
    const [companyAdresa, setCompanyAdresa] = useState('');
    const [companyCont, setCompanyCont] = useState('');
    const [companyBank, setCompanyBank] = useState('');

    useEffect(() => {
        getClients(currentUser.uid)
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    const newClientHandle = (e) => {
        e.preventDefault();
        if (update) {
            updateClient(companyId, companyName, companyCIF, companyRegCom, companyAdresa,
                companyBank, companyCont)
            setAddNew(false)
            resetValues();
            getClients(currentUser.uid)
            setUpdate(false)
        }
        else {
            newClient(companyName, companyCIF, companyRegCom, companyAdresa,
                companyBank, companyCont)
            setAddNew(false)
            resetValues();
            getClients(currentUser.uid)
        }
    }

    const resetValues = () => {
        setCompanyName('')
        setCompanyCIF('')
        setCompanyRegCom('')
        setCompanyAdresa('')
        setCompanyCont('')
        setCompanyBank('')
    }

    const editClientHandle = (id) => {
        const compFilter = clients.filter((comp) => comp.cliId === id)

        const currentComp = compFilter[0];

        setCompanyId(currentComp.cliId)
        setCompanyName(currentComp.name)
        setCompanyCIF(currentComp.cif)
        setCompanyRegCom(currentComp.regCom)
        setCompanyAdresa(currentComp.address)
        setCompanyCont(currentComp.account)
        setCompanyBank(currentComp.bank)
        setUpdate(true)
        setAddNew(true)
    }

    const toggleForm = () => {
        if (currentUser.emailVerified) {
            setAddNew(!addNew)
            addNew && resetValues();
            setUpdate(false)
        }
        else {
            const ask = window.confirm('Verify email to add a client');
            if (ask) {
                history.push('/profile')
            }
        }
    }

    const deleteClientHandle = (id) => {
        deleteClient(id);
        getClients(currentUser.uid)
    }

    const editField = (id) => {
        return (
            <>
                <span title="edit" onClick={() => editClientHandle(id)}
                    style={{ padding: '5px' }} className="btn"></span>

                <span title="delete" onClick={() => deleteClientHandle(id)}
                    style={{ cursor: 'pointer' }}></span>
            </>
        )
    }

    return (
        <div className="main-menu">
            <h1 style={{ paddingTop: '5%', fontSize: '50px' }}>Clients</h1>
            <NavigationBar />
            <div className="main-select">
                {clients.length > 0 ?
                    <table className="table-item-preview">
                        <tbody>
                            <tr>
                                <th>Nr.</th>
                                <th>Name</th>
                                <th>Address</th>
                                <th>CIF</th>
                                <th>Actions</th>
                            </tr>
                            {
                                clients.map((comp, i) => {
                                    return (
                                        <tr key={i}>
                                            <td>
                                                {i + 1}
                                            </td>
                                            <td>
                                                {comp.name}
                                            </td>
                                            <td>
                                                {comp.address}
                                            </td>
                                            <td>
                                                {comp.cif}
                                            </td>
                                            <td>
                                                {editField(comp.cliId)}
                                            </td>
                                        </tr>
                                    )
                                })
                            }
                        </tbody>
                    </table>
                    :
                    <div>No clients..
                        <span onClick={() => toggleForm()}
                            style={{ textDecoration: 'underline', cursor: 'pointer', userSelect: 'none' }}>
                            add one
                        </span>
                    </div>
                }

                <div className="btn" onClick={() => toggleForm()}>Add new client</div>
            </div>
            {addNew &&
                <div className="main-select">
                    <form onSubmit={newClientHandle}>
                        {!update ? <h2>New client</h2> : <h2>{companyName}</h2>}
                        <div className="form-structure">
                            <div className="form-labels">
                                <label>Nume:</label>
                                <label>CIF:</label>
                                <label>Reg. com.:</label>
                                <label>Adresa:</label>
                                <label>Banc:</label>
                                <label>Cont:</label>
                            </div>
                            <div className="form-input">
                                <input className="invoice-input" value={companyName}
                                    type='text' onChange={(e) => setCompanyName(e.target.value)} required />
                                <input className="invoice-input"
                                    value={companyCIF}
                                    type='text' onChange={(e) =>
                                        setCompanyCIF(e.target.value.toUpperCase())} required />
                                <input className="invoice-input"
                                    value={companyRegCom}
                                    type='text' onChange={(e) =>
                                        setCompanyRegCom(e.target.value.toUpperCase())} required />
                                <input className="invoice-input" value={companyAdresa}
                                    type='text' onChange={(e) => setCompanyAdresa(e.target.value)} required />
                                <input className="invoice-input" value={companyBank}
                                    type='text' onChange={(e) => setCompanyBank(e.target.value)} />
                                <input className="invoice-input"
                                    value={companyCont}
                                    type='text' onChange={(e) =>
                                        setCompanyCont(e.target.value.toUpperCase())} />
                            </div>
                        </div>
                        <br />
                        {!update ?
                            <input style={{ marginTop: '20px' }} className="btn" type='submit' value='Add client' />
                            :
                            <input style={{ marginTop: '20px' }} className="btn" type='submit' value='Update client' />
                        }
                    </form>
                </div>
            }
        </div>
    )
}

export default Clients
