import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';

import { BrowserView, MobileView } from 'react-device-detect';
import AppMobile from './AppMobile';


ReactDOM.render(
  <React.StrictMode>
    <BrowserView>
      <App />
    </BrowserView>
    <MobileView>
      <AppMobile />
    </MobileView>
  </React.StrictMode>,
  document.getElementById('root')
);

