import { useState, useEffect } from "react";
import { useAuth } from "../Contexts/AuthContext";
import { useHistory } from "react-router-dom";
import NavigationBar from "./NavigationBar";

const MyCompanies = () => {
    const history = useHistory(); 

    const { userCompanies, 
        newCompany, 
        getCompanies, 
        currentUser, 
        updateCompany, 
        deleteCompany
    } = useAuth();

    const [addNew, setAddNew] = useState(false)
    const [update, setUpdate] = useState(false)
    //company form
    const [companyId, setCompanyId] = useState('')
    const [companyName, setCompanyName] = useState('');
    const [companyCIF, setCompanyCIF] = useState('');
    const [companyRegCom, setCompanyRegCom] = useState('');
    const [companyAdresa, setCompanyAdresa] = useState('');
    const [companyCapSocial, setCompanyCapSocial] = useState(0);
    const [companyCont, setCompanyCont] = useState('');
    const [companyBank, setCompanyBank] = useState('');
    const [companyTelefon, setCompanyTelefon] = useState('');
    const [companyTVA, setCompanyTVA] = useState(19);
    const [companyTVApay, setCompanyTVApay] = useState(true);
    const [companyLogo, setCompanyLogo] = useState('');
    const [invoiceTerm, setInvoiceTerm] = useState(15);
    const [invoiceSeries, setInvoiceSeries] = useState('');
    const [invoiceNr, setInvoiceNr] = useState('');

    const [delName, setDelName] = useState('');
    const [delCNP, setDelCNP] = useState('');
    const [delSeries, setDelSeries] = useState('');

    useEffect(() => {
        getCompanies(currentUser.uid)
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    // const updateLogo = (e) => {
    //     setCompanyLogo('');
    //     if (e) setCompanyLogo(URL.createObjectURL(e));
    // }

    const newComp = (e) => {
        e.preventDefault();
        if(update) {
            updateCompany(companyId, companyName, companyCIF, companyRegCom, companyAdresa,
                companyCapSocial, companyBank, companyCont,
                companyTelefon, companyTVA, companyTVApay, companyLogo,
                invoiceSeries, invoiceNr, invoiceTerm, 
                delName, delSeries, delCNP)
            setAddNew(false)
            resetValues();
            getCompanies(currentUser.uid)
            setUpdate(false)
        }
        else {
            newCompany(companyName, companyCIF, companyRegCom, companyAdresa, 
                companyCapSocial, companyBank, companyCont,
                companyTelefon, companyTVA, companyTVApay, companyLogo,
                invoiceSeries, invoiceNr, invoiceTerm,
                delName, delSeries, delCNP)
            setAddNew(false)
            resetValues();
            getCompanies(currentUser.uid)
        }
    }

    const resetValues = () => {
        setCompanyName('')
        setCompanyCIF('')
        setCompanyRegCom('')
        setCompanyAdresa('')
        setCompanyCapSocial(0)
        setCompanyCont('')
        setCompanyBank('')
        setCompanyTelefon('')
        setCompanyTVA(19)
        setCompanyTVApay(true)
        setCompanyLogo('')
        setInvoiceSeries('')
        setInvoiceNr('')
        setInvoiceTerm(15)
        setDelName('')
        setDelSeries('')
        setDelCNP('')
    }

    const editComp = (id) => {
        const compFilter = userCompanies.filter((comp) => comp.compId === id)

        const currentComp = compFilter[0];

        setCompanyId(currentComp.compId)
        setCompanyName(currentComp.name)
        setCompanyCIF(currentComp.cif)
        setCompanyRegCom(currentComp.regCom)
        setCompanyAdresa(currentComp.address)
        setCompanyCapSocial(currentComp.capSoc)
        setCompanyCont(currentComp.account)
        setCompanyBank(currentComp.bank)
        setCompanyTelefon(currentComp.phone)
        setCompanyTVA(currentComp.tva)
        setCompanyTVApay(currentComp.tvaPay)
        setCompanyLogo(currentComp.logo)
        setInvoiceSeries(currentComp.serie)
        setInvoiceNr(currentComp.nrStart)
        setInvoiceTerm(currentComp.term)
        setDelName(currentComp.delName)
        setDelSeries(currentComp.delCI)
        setDelCNP(currentComp.delCNP)
        setUpdate(true)
        setAddNew(true)
    }

    const toggleForm = () => {
        if (currentUser.emailVerified){
            setAddNew(!addNew)
            addNew && resetValues();
            setUpdate(false)
        }
        else{
            const ask = window.confirm('Verify email to create a company');
            if (ask) {
                history.push('/profile')
            }
        }
    }

    const deleteCom = (id) => {
        deleteCompany(id);
        getCompanies(currentUser.uid)
    }

    const editField = (id) => {
        return(
            <>
                <span title="edit" onClick={() => editComp(id)}
                style={{padding:'5px'}} className="btn"></span>

                <span title="delete" onClick={() => deleteCom(id)}
                style={{cursor:'pointer'}}></span>
            </>
        )
    }

    return (
        <div className="main-menu">
            <h1 style={{ paddingTop: '5%', fontSize: '50px' }}>My Companies</h1>
            <NavigationBar />
            <div className="main-select">
                { userCompanies.length > 0 ?
                    <table className="table-item-preview">
                        <tbody>
                            <tr>
                                <th>Nr.</th>
                                <th>Name</th>
                                <th>Address</th>
                                <th>CIF</th>
                                <th>Actions</th>
                            </tr>
                            {
                                userCompanies.map((comp, i) => {
                                    return(
                                        <tr key={i}>
                                            <td>
                                                {i + 1}
                                            </td>
                                            <td>
                                                {comp.name}
                                            </td>
                                            <td>
                                                {comp.address}
                                            </td>
                                            <td>
                                                {comp.cif}
                                            </td>
                                            <td>
                                                {editField(comp.compId)}
                                            </td>
                                        </tr>
                                    )
                                })
                            }
                        </tbody>
                    </table>
                :
                    <div>No companies.. 
                        <span onClick={() => toggleForm()} 
                        style={{textDecoration:'underline', cursor:'pointer', userSelect:'none'}}>
                            add one
                        </span>
                    </div>
                }
                
                <div className="btn" onClick={() => toggleForm()}>Add new company</div>

            </div>

            {addNew &&
                <div className="main-select">
                    <form onSubmit={newComp}>
                        {!update ? <h2>New company</h2> : <h2>{companyName}</h2>}
                        <div className="form-structure">
                            <div className="form-labels">
                                <label>Nume:</label>
                                <label>CIF:</label>
                                <label>Reg. com.:</label>
                                <label>Adresa:</label>
                                <label>Cap. social:</label>
                                <label>Banc:</label>
                                <label>Cont:</label>
                                <label>Telefon:</label>
                                <label>Cot TVA:</label>
                                <label>TVA ncasare:</label>
                                <label>Serie facturi:</label>
                                <label>Nr nceput:</label>
                                <label>Termen plat:</label>
                            </div>
                            <div className="form-input">
                                <input className="invoice-input" value={companyName}
                                    type='text' onChange={(e) => setCompanyName(e.target.value)} required />
                                <input className="invoice-input"
                                    value={companyCIF}
                                    type='text' onChange={(e) =>
                                        setCompanyCIF(e.target.value.toUpperCase())} required />
                                <input className="invoice-input"
                                    value={companyRegCom}
                                    type='text' onChange={(e) =>
                                        setCompanyRegCom(e.target.value.toUpperCase())} required />
                                <input className="invoice-input" value={companyAdresa}
                                    type='text' onChange={(e) => setCompanyAdresa(e.target.value)} required />
                                <input className="invoice-input" value={companyCapSocial} min={0}
                                    type='number' onChange={(e) => setCompanyCapSocial(e.target.value)} />
                                <input className="invoice-input" value={companyBank}
                                    type='text' onChange={(e) => setCompanyBank(e.target.value)} />
                                <input className="invoice-input"
                                    value={companyCont}
                                    type='text' onChange={(e) => 
                                    setCompanyCont(e.target.value.toUpperCase())} />
                                <input className="invoice-input" value={companyTelefon} pattern="[0-9]{10}"
                                    type='tel' onChange={(e) => setCompanyTelefon(e.target.value)} />
                                <input className="invoice-input" value={companyTVA} min={0}
                                    type='number' onChange={(e) => setCompanyTVA(e.target.value)} required />
                                <select className="invoice-input"
                                    value={companyTVApay} onChange={(e) => setCompanyTVApay(e.target.value)}>
                                    <option value={true}>DA</option>
                                    <option value={false}>NU</option>
                                </select>
                                {/* TO DO FROM SERIE */}
                                <input className="invoice-input" value={invoiceSeries}
                                    type='text' onChange={(e) => setInvoiceSeries(e.target.value)} required />
                                <input className="invoice-input" value={invoiceNr} min={0}
                                    type='number' onChange={(e) => setInvoiceNr(e.target.value)} required/>
                                <input className="invoice-input" value={invoiceTerm} min={0}
                                    type='number' onChange={(e) => setInvoiceTerm(e.target.value)} required/>
                            </div>
                        </div>
                        <h4>Delegat</h4>
                        <div className="form-structure">
                            <div className="form-labels">
                                <label>Nume:</label>
                                <label>Serie CI:</label>
                                <label>CNP:</label>
                            </div>
                            <div className="form-input">
                                <input className="invoice-input" value={delName}
                                    type='text' onChange={(e) => setDelName(e.target.value)} />
                                <input className="invoice-input" value={delSeries}
                                    type='text' onChange={(e) => setDelSeries(e.target.value)} />
                                <input className="invoice-input" value={delCNP}
                                    type='text' onChange={(e) => setDelCNP(e.target.value)} />
                            </div>
                        </div>
                        {/* <input className="invoice-upload-file" accept="image/*"
                            type='file' onChange={(e) =>
                                updateLogo(e.target.files[0])} /> */}
                        <br />
                        {!update ?
                        <input style={{marginTop:'20px'}} className="btn" type='submit' value='Add company' />
                        :
                        <input style={{ marginTop: '20px' }} className="btn" type='submit' value='Update company' />
                        }
                    </form>
                    {/* {companyLogo && <img className="img-preview" src={companyLogo} alt="company logo" />} */}
                </div>
            }
        </div>
    )
}

export default MyCompanies
