import { useState } from "react";
import { Link } from "react-router-dom";
import { useAuth } from "../Contexts/AuthContext";

const Login = () => {

    const [mail, setMail] = useState('');
    const [pass, setPass] = useState('');
    //const [error, setError] = useState('');

    const { login, error, resetPassword, dbLoading } = useAuth()

    const handleLogin = async (e) => {
        e.preventDefault();
        login(mail, pass);
    }

    const sendPasswordResetEmail = async () => {
        resetPassword(mail);
    };

    return (
        <div className="main-menu">
            <h1 style={{ paddingTop: '5%', fontSize: '50px' }}>Log in</h1>
            <div className="main-select">
                <form onSubmit={handleLogin}>
                    <h4 style={{color:'red'}}>{error}</h4>
                    <h2>Email</h2>
                    <input type='email' name="email" className="user-input" autoComplete="on"
                        value={mail} onChange={(e) => setMail(e.target.value)} required />
                    <h2>Password</h2>
                    <input type='password' name="password" className="user-input" autoComplete="on" 
                        value={pass} onChange={(e) => setPass(e.target.value)} required/>
                    <br />
                    {!dbLoading && <input className="btn" type='submit' value='Log in' />}
                </form>
                <Link to={'/register'}>No account? Register</Link>
                {mail && <div className="btn" onClick={sendPasswordResetEmail}>Forgot password?</div>}
            </div>
        </div>
    )
}

export default Login
