import { useState, useEffect } from "react";
import { useAuth } from "../Contexts/AuthContext";
import NavigationBar from "./NavigationBar";

const MyProfile = () => {
    const frmt = new Intl.NumberFormat('en-US');

    const { currentUser, 
        userCompanies,
        clients, 
        verifyEmail, 
        updateProfile, 
        getCompanies,
        getClients,
        getInvoices,
        invoices,
        invoicesEarnings
    } = useAuth();

    const [info, setInfo] = useState(false);
    const [edit, setEdit] = useState(false)

    const [name, setName] = useState(currentUser.displayName)
    const [email, setEmail] = useState(currentUser.email)
    const [phone, setPhone] = useState(currentUser.phoneNumber)
    
    useEffect(() => {
        getCompanies(currentUser.uid)
        getClients(currentUser.uid)
        getInvoices(currentUser.uid)
        //getTotalEarnings()
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    const handleUpdateProfile = (e) => {
        e.preventDefault();
        updateProfile(name, email, phone)
        setEdit(false)
    }

    const userInfo = () => {
        if(edit){
            return(
                <form onSubmit={handleUpdateProfile}>
                    <h3>Name: 
                        <input style={{fontSize:'1.5em', textAlign:'center'}} 
                            type='text' name='name' required
                            value={name} onChange={(e) => setName(e.target.value)}/>
                    </h3>
                    <h3>Email:
                        <input style={{ fontSize: '1.5em', textAlign: 'center' }}
                            type='email' name='email' required
                            value={email} onChange={(e) => setEmail(e.target.value)} />
                    </h3>
                    <h3>Phone:
                        <input style={{ fontSize: '1.5em', textAlign: 'center' }}
                            type='tel' name='tel' required
                            value={phone} onChange={(e) => setPhone(e.target.value)} />
                    </h3>
                    <input className="btn" type='submit' value='Save' />
                    <br />
                    <div onClick={() => setEdit(false)} style={{marginTop:'15px'}} 
                    className="btn log-out">Cancel</div>
                </form>
            )
        }
        else{
            return(
                <>
                    <h3>Name: <span style={{ fontSize: '1.5em' }}>{currentUser.displayName}</span></h3>
                    <h3>Email: <span style={{ fontSize: '1.5em' }}>{currentUser.email}</span></h3>
                    <h3>Phone: <span style={{ fontSize: '1.5em' }}>{currentUser.phoneNumber}</span></h3>
                    <div onClick={() => setEdit(true)} className="btn">Edit</div>
                    <br />
                </>
            )
        }
    }

    return (
        <div className="main-menu">
            {currentUser ? 
                <h2 style={{ paddingTop: '5%', fontSize: '50px' }}>{currentUser.displayName}'s profile</h2>
                : <h1 style={{ paddingTop: '5%', fontSize: '50px' }}>Profile</h1>}
            <NavigationBar />
            <div className="main-select">
                <div onClick={() => setInfo(!info)} className="btn">
                    User info
                </div>

                {info && 
                    <div style={{ border: '1px solid rgb(41, 41, 41)', 
                    padding:'20px', 
                    textAlign:'center',
                    borderRadius:'15px'}}>
                        {userInfo()}
                        <small>Created: {currentUser.metadata.creationTime}</small>
                    </div>
                }

                {!currentUser.emailVerified && 
                <div className="btn log-out" onClick={() => verifyEmail(currentUser.email)}>VERIFY EMAIL</div>}

                <table className="table-item-preview">
                    <tbody>
                        <tr>
                            <th>Companies</th>
                            <th>Clients</th>
                            <th>Invoices generated</th>
                            <th>Earnings</th>
                        </tr>
                        <tr>
                            <td>{userCompanies.length}</td>
                            <td>{clients.length}</td>
                            <td>{invoices.length}</td>
                            <td>{frmt.format(invoicesEarnings)} lei</td>
                        </tr>
                    </tbody>
                </table>
            </div>
        </div>
    )
}

export default MyProfile
