import { useEffect, useState } from "react";
import NavigationBar from "./NavigationBar"
import { useAuth } from "../Contexts/AuthContext";
import { useHistory } from "react-router-dom";

const History = ({ generateInvoice }) => {
    const frmt = new Intl.NumberFormat('en-US');
    const history = useHistory();

    const [loading, setLoading] = useState(false);
    const [openFilter, setOpenFilter] = useState(false);
    const [filteredInvoices, setFilteredInvoices] = useState([]);

    //filters
    const [filters, setFilters] = useState({
        invoiceSeries: '',
        invoiceDate: '',
        companyName: '',
        clientName: '',
        range:{
            minVal: '',
            maxVal: ''
        }
    });

    const { 
        currentUser,
        getInvoices,
        invoices,
        userCompanies,
        getCompanies,
        clients,
        getClients,
        deleteInvoice
    } = useAuth();

    useEffect(() => {
        getInvoices(currentUser.uid)
        getCompanies(currentUser.uid)
        getClients(currentUser.uid)
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    useEffect(() => {
        setFilteredInvoices(invoices)
    }, [invoices])

    useEffect(() => {
        invoices.length && filterInvoiceShow(filters);
    // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [filters])

    const filterInvoiceShow = (f) => {
        const getValue = value => (typeof value === 'string' ? value.toUpperCase() : value);
        const filterKeys = Object.keys(f);
        const filterRes = invoices.filter( inv => {
            return filterKeys.every( key => {
                if(!f[key].length && key !== 'range') return invoices;
                if(key === 'range'){
                    if (!f[key].maxVal.length && !f[key].minVal.length) return invoices;
                    if (!f[key].maxVal.length) return getTotalOfOnvoice(inv.items) > parseInt(f[key].minVal);
                    if (!f[key].minVal.length) return getTotalOfOnvoice(inv.items) < parseInt(f[key].maxVal);
                    return getTotalOfOnvoice(inv.items) > parseInt(f[key].minVal) && 
                        getTotalOfOnvoice(inv.items) < parseInt(f[key].maxVal);
                }
                return getValue(f[key]).includes(getValue(inv[key]))
            })
        })
        setFilteredInvoices(filterRes)
    }

    const formatDate = (d) => {
        const res = new Date(d);
        const mnth = res.getMonth()+1 < 10 ? ("0" + res.getMonth()+1).slice(-2) : res.getMonth()+1
        const dy = res.getDate() < 10 ? ("0" + res.getDate()).slice(-2) : res.getDate()
        return `${dy}/${mnth}/${res.getFullYear()}`;
    }

    const dateFilter = (d) => {
        if(!d) return '';
        const res = new Date(d);
        const mnth = res.getMonth() + 1 < 10 ? ("0" + res.getMonth() + 1).slice(-2) : res.getMonth() + 1
        const dy = res.getDate() < 10 ? ("0" + res.getDate()).slice(-2) : res.getDate()
        return `${res.getFullYear()}-${mnth}-${dy}`
    }

    const viewInvoice = async (id) => {
        setLoading(true);

        const filterInvoice = invoices.filter(invoice => invoice.invId === id);

        const currentInvoice = filterInvoice[0];

        return await new Promise(resolve => setTimeout(() => {resolve(setLoading(false))}, 1500))
        .then(() => {
            generateInvoice(currentInvoice);
            history.push("/generated-invoice");
        })
    }

    const resetFilters = () => {
        setFilteredInvoices(invoices)
    }

    const filterTemplate = () => {
        return(
            <div className="main-select" style={{marginBlock:'15px'}}>
                <div className="form-structure">
                    <div className="form-labels filter-labels">
                        <div>Series:</div>
                        <div>Date created:</div>
                        <div>Company:</div>
                        <div>Client:</div>
                    </div>
                    <div className="form-input filter-labels">
                        <input value={filters.invoiceSeries} 
                            onChange={(e) => setFilters({ ...filters, invoiceSeries: e.target.value })}
                            type='text' className="filter-input" />
                        <input style={{marginBlock:'0', marginTop:'-30px'}}
                            onChange={(e) => setFilters({ ...filters, invoiceDate: dateFilter(e.target.value) })}
                            type='date' className="filter-input" />  

                        <select className="filter-input"
                            onChange={(e) => setFilters({ ...filters, companyName: e.target.value })}>
                            <option value={''}>-</option>
                            {userCompanies.map((comp, i) => {
                                return (
                                    <option key={i} value={comp.name}>{comp.name}</option>
                                )
                            })}
                        </select>

                        <select className="filter-input"
                            onChange={(e) => setFilters({ ...filters, clientName: e.target.value })}>
                            <option value={''}>-</option>
                            {clients.map((cli, i) => {
                                return (
                                    <option key={i} value={cli.name}>{cli.name}</option>
                                )
                            })}
                        </select>
                    </div>
                </div>
                <div style={{marginTop:'-10px'}}>
                    <div>Value range:</div>
                    <input type='number' className="filter-input" style={{width:'25%'}} 
                        onChange={(e) => 
                            setFilters({ ...filters, range:{...filters.range, minVal: e.target.value} })}/>
                    -
                    <input type='number' className="filter-input" style={{ width: '25%' }}
                        onChange={(e) => 
                            setFilters({ ...filters, range: { ...filters.range, maxVal: e.target.value} })} />
                </div>
                <small onClick={resetFilters}
                style={{marginBlock:'-10px', textDecoration:'underline', cursor:'pointer', userSelect:'none'}}>
                    reset filters
                </small>
            </div>
        )
    }

    const getTotalOfOnvoice = (itms) => {
        let total = 0;

        itms.map(itm => {
            return total += parseFloat(itm.val) + parseFloat(itm.tva);
        })

        return total.toFixed(2);
    }

    const deleteInv = (id) => {
        deleteInvoice(id);
        getInvoices(currentUser.uid)
    }

    const invoiceTemplate = () => {
        return(
            <>
            {filteredInvoices.length > 0 ?
                filteredInvoices.map(invoice => {
                    return(
                        <div style={{marginBlock:'20px'}} key={invoice.invId} className="main-select history-main">
                            <h3>
                                <small>Invoice series: </small>
                                {invoice.invoiceSeries}
                                <small> nr: </small>
                                {invoice.invoiceNr}
                                <small> from: </small>
                                {formatDate(invoice.invoiceDate)}
                            </h3>
                            <div className="history-row">
                                <div>
                                    <div>Furnizor:</div>
                                    <h3>{invoice.companyName}</h3>
                                    <div style={{ fontStyle: 'italic' }}>{invoice.companyCIF}</div>
                                </div>
                                <div>
                                    <div>Client:</div>
                                    <h3>{invoice.clientName}</h3>
                                    <div style={{ fontStyle: 'italic' }}>{invoice.clientCIF}</div>
                                </div>
                            </div>
                            <div>Value: {frmt.format(getTotalOfOnvoice(invoice.items))} lei</div>
                            <div onClick={() => viewInvoice(invoice.invId)}
                            style={{ textDecoration: 'underline', cursor: 'pointer', userSelect: 'none' }}>
                                Preview invoice
                            </div>
                            <small onClick={() => deleteInv(invoice.invId)} title="Delete invoice"
                                style={{ textDecoration: 'underline', cursor: 'pointer', userSelect: 'none',
                                color:'red', marginTop:'-20px' }}>
                                Delete
                            </small>
                        </div>
                    )
                })
                :
                <div>No invoices..</div>
            }
            </>
        )
    }

    return (
        <div className="main-menu">
            <h1 style={{ paddingTop: '5%', fontSize: '50px' }}>History</h1>
            <NavigationBar />
            <div onClick={() => setOpenFilter(!openFilter)}
                style={{ textDecoration: 'underline', cursor: 'pointer', userSelect: 'none', marginBottom:'15px'}}>
                    Filters
            </div>
            {openFilter && filterTemplate()}
            {invoices.length > 0 && <div>Showing {filteredInvoices.length} invoices</div>}
            {!loading ? invoiceTemplate() : <div id="overlay"><div className="spinner"></div></div>}
        </div>
    )
}

export default History
