import { Link, useHistory } from "react-router-dom";
import { signOutAccount } from "../firebase";
import { useAuth } from "../Contexts/AuthContext";

const MainMenu = () => {
    const history = useHistory();

    const { currentUser } = useAuth();

    const customButton = { fontSize: '30px', width: '50% '};

    const signOut = () => {
        signOutAccount();
        history.push('/');
    }

    return (
        <div className="main-menu" >
            <h1 style={{paddingTop:'5%', fontSize:'50px'}}>INVOICE GENERATOR</h1>
            {currentUser && <h3>Hello, {currentUser.displayName}</h3>}
            <div className="main-select">
                <Link className="btn" style={customButton} 
                    to={'/new-invoice'}>New Invoice</Link>
                {currentUser ?
                    <>
                        <Link className="btn" style={customButton}
                            to={'/profile'}>My profile</Link>
                            
                        <Link className="btn" style={customButton}
                            to={'/companies'}>My companies</Link>

                        <Link className="btn" style={customButton}
                            to={'/clients'}>Clients</Link>

                        <Link className="btn" style={customButton}
                            to={'/products'}>Products</Link>

                        <Link className="btn" style={customButton}
                            to={'/history'}>History</Link>

                        <div className="btn log-out" onClick={signOut}>Log out</div>
                    </>
                    :
                    <div style={{userSelect:'none'}}>Free version</div>
                }
            </div>
        </div>
    )
}

export default MainMenu
