import { useState } from "react";
import { Link } from "react-router-dom";
import { useAuth } from "../Contexts/AuthContext";

const Register = () => {

    const [name, setName] = useState('');
    const [mail, setMail] = useState('');
    const [phone, setPhone] = useState('');
    const [pass, setPass] = useState('');

    const { signup, error, dbLoading } = useAuth()

    const handleRegister = async (e) => {
        e.preventDefault();
        signup(name, mail, phone, pass);
    }



    return (
        <div className="main-menu">
            <h1 style={{ paddingTop: '5%', fontSize: '50px' }}>Register</h1>
            <div className="main-select">
                <form onSubmit={handleRegister}>
                    <h4 style={{ color: 'red' }}>{error}</h4>
                    <h2>Email</h2>
                    <input type='email' name="email" className="user-input" autoComplete="on"
                        value={mail} onChange={(e) => setMail(e.target.value)} required />
                    <h2>Name</h2>
                    <input type='text' name="name" className="user-input" autoComplete="on"
                        value={name} onChange={(e) => setName(e.target.value)} required />
                    <h2>Phone</h2>
                    <input type='tel' name="tel" className="user-input" autoComplete="on"
                        value={phone} onChange={(e) => setPhone(e.target.value)} required />
                    <h2>Password</h2>
                    <input type='password' name="password" className="user-input" autoComplete="on"
                        value={pass} onChange={(e) => setPass(e.target.value)} required />
                    <br />
                    {!dbLoading && <input className="btn" type='submit' value='Register' />}
                </form>
                <Link to={'/login'}>Log in?</Link>
            </div>
        </div>
    )
}

export default Register
