import React from "react";
import { useState } from "react";

export const InvoiceStructure = React.forwardRef(({ data, nota, border }, ref) => {

    const [currentTemplate, setCurrentTemplate] = useState(0);

    const frmt = new Intl.NumberFormat('en-US');

    const formatDate = (d) => {
        const res = new Date(d);
        const mnth = res.getMonth() + 1 < 10 ? ("0" + res.getMonth() + 1).slice(-2) : res.getMonth() + 1
        const dy = res.getDate() < 10 ? ("0" + res.getDate()).slice(-2) : res.getDate()
        return `${dy}/${mnth}/${res.getFullYear()}`;
    }

    const getTotal = () => {
        let total = 0;
        data.items.map(item => total += (parseFloat(item.val) + parseFloat(item.tva)))
        return frmt.format(total)
    }

    const getPartial = (tva) => {
        let total = 0
        if (!tva) {
            data.items.map(item => total += parseFloat(item.val))
            return frmt.format(total)
        }
        else {
            data.items.map(item => total += parseFloat(item.tva))
            return frmt.format(total)
        }
    }

    const tp1 = () => {
        return (
            <div className="print-bg" ref={ref}>
                <div className="page a4">
                    <div className="page-padding">
                        <div className="page-row">
                            {data.companyLogo &&
                                <img className="img-invoice" src={data.companyLogo} alt="company logo" />
                            }

                            <div className="invoice-top-data">
                                <h1>FACTUR</h1>
                                <table className="table-item-preview table-invoice-top">
                                    <tbody>
                                        <tr>
                                            <th>Serie</th>
                                            <th>Numr</th>
                                            <th>Dat</th>
                                        </tr>
                                        <tr>
                                            <td>{data.invoiceSeries}</td>
                                            <td>{data.invoiceNr}</td>
                                            <td>
                                                {formatDate(data.invoiceDate)}
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>
                        <div className="page-row company-table">
                            <div className="company-table-inside" style={{ borderRight: '2px solid #ddd' }}>
                                <div>Furnizor:</div>
                                <div>{data.companyName}</div>
                                <div>CIF: {data.companyCIF}</div>
                                <div>Reg. com.: {data.companyRegCom}</div>
                                <div>Adres: {data.companyAdresa}</div>
                                <div>Telefon: {data.companyTelefon}</div>
                                <div>Cont: {data.companyCont}</div>
                                <div>Banc: {data.companyBank}</div>
                                <div>Cap. social: {data.companyCapSocial}</div>
                                {data.companyTVA > 0 &&
                                    <>
                                        <div>
                                            Cot TVA: {data.companyTVA}% - {data.companyTVApay && 'TVA la ncasare'}
                                        </div>
                                    </>
                                }
                            </div>
                            <div className="company-table-inside" style={{ marginLeft: '10px' }}>
                                <div>Client:</div>
                                <div>{data.clientName}</div>
                                <div>CIF: {data.clientCIF}</div>
                                <div>Reg. com.: {data.clientRegCom}</div>
                                <div>Adres: {data.clientAdresa}</div>
                                <div>Cont: {data.clientCont}</div>
                                <div>Banc: {data.clientBank}</div>
                            </div>
                        </div>

                        <table className={border ? "table-item-preview table-invoice-services" :
                            "table-item-preview table-invoice-services table-nomargin"}>
                            <tbody>
                                <tr>
                                    <th>Nr. crt</th>
                                    <th>Denumirea produselor sau a serviciilor</th>
                                    <th>U.M.</th>
                                    <th>Cantitate</th>
                                    <th>Pre unitar</th>
                                    <th>Valoare lei</th>
                                    {data.companyTVA > 0 && <th>Valoare TVA</th>}
                                </tr>
                                {data.items.map((item, i) => {
                                    return (
                                        <tr key={i}>
                                            <td>{i + 1}</td>
                                            <td>{item.name}</td>
                                            <td>{item.um}</td>
                                            <td>{frmt.format(parseFloat(item.qt))}</td>
                                            <td>{frmt.format(parseFloat(item.unitPrice))}</td>
                                            <td>{frmt.format(parseFloat(item.val))}</td>
                                            {data.companyTVA > 0 && <td>{frmt.format(parseFloat(item.tva))}</td>}
                                        </tr>
                                    )
                                })}
                                <tr style={{ borderTop: '4px solid #ddd' }}>
                                    <td colSpan={1} rowSpan={2} style={{ backgroundColor: 'white' }} >
                                        <div style={{ textAlign: 'right' }}>
                                            <div>Delegat:</div>
                                            <div>CNP:</div>
                                            <div>C.I.:</div>
                                            <div>Transport:</div>
                                        </div>
                                    </td>
                                    <td colSpan={3} rowSpan={2} 
                                    style={{ backgroundColor: 'white', verticalAlign: 'top' }}>
                                        <div style={{ textAlign: 'left' }}>
                                            <div>{data.delName}</div>
                                            <div>{data.delCNP}</div>
                                            <div>{data.delSeries}</div>
                                            <div>{data.transport}</div>
                                        </div>
                                    </td>
                                    <td colSpan={1} rowSpan={2}>TOTAL:</td>
                                    {data.companyTVA > 0 && <>
                                        <td>{getPartial(false)}</td>
                                        <td>{getPartial(true)}</td></>}
                                </tr>
                                <tr>
                                    <td colSpan={data.companyTVA > 0 ? 2 : 1}>{getTotal()} lei</td>
                                </tr>
                                <tr>
                                    <td colSpan={4} style={{ paddingBottom: '20%', backgroundColor: 'white' }}>
                                        Semntur i tampil furnizor
                                    </td>
                                    <td colSpan={3} style={{ paddingBottom: '20%', backgroundColor: 'white' }}>
                                        Semntur de primire
                                    </td>
                                </tr>
                            </tbody>
                        </table>
                        <div style={{ textAlign: 'center' }}>
                            Termen de plat: {data.invoiceTerm} zile de la emiterea facturii.
                        </div>
                        {nota && <div style={{ textAlign: 'center', marginTop: '20px' }}>
                            <small >
                                Not: Potrivit art. 319, alin. 29 din Legea nr.227/2015 privind Codul fiscal,<br />
                                <span style={{ fontStyle: 'italic' }}>
                                    "semnarea si stampilarea facturilor nu constituie elemente 
                                    obligatorii pe care trebuie sa le contina factura".
                                </span>
                            </small>
                        </div>}
                    </div>
                </div>
            </div>
        )
    }

    const tp2 = () => {
        return(
            <div className="print-bg" ref={ref}>
                <div className="page a4">
                    <div className="page-padding">
                        <div className="page-row">
                            {data.companyLogo &&
                                <img className="img-invoice" src={data.companyLogo} alt="company logo" />
                            }
                            <div className="invoice-top-data">
                                <table className="table-item-preview table-invoice-top table-nomargin">
                                    <tbody>
                                        <tr>
                                            <th style={{fontSize:'2em'}}>Factura</th>
                                            <th></th>
                                            <th>{data.invoiceSeries} {data.invoiceNr}</th>
                                        </tr>
                                        <tr>
                                            <td colSpan={3} style={{textAlign:'left'}}>
                                                Data emiterii: {formatDate(data.invoiceDate)}
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>

                        <div className="page-row company-table" style={{border:'none'}}>
                            <div className="company-table-inside">
                                
                                <div>Furnizor:</div>

                                <table className="table-nomargin table-strong-title">
                                    <tbody>
                                        <tr>
                                            <td colSpan={2}>{data.companyName}</td>
                                        </tr>
                                        <tr>
                                            <td>CIF:</td>
                                            <td>{data.companyCIF}</td>
                                        </tr>
                                        <tr>
                                            <td>Reg. com.:</td>
                                            <td>{data.companyRegCom}</td>
                                        </tr>
                                        <tr>
                                            <td>Adres:</td>
                                            <td>{data.companyAdresa}</td>
                                        </tr>
                                        <tr>
                                            <td>Telefon:</td>
                                            <td>{data.companyTelefon}</td>
                                        </tr>
                                        {data.companyCont && <tr>
                                            <td>Cont:</td>
                                            <td>{data.companyCont}</td>
                                        </tr>}
                                        {data.companyBank && <tr>
                                            <td>Banc:</td>
                                            <td>{data.companyBank}</td>
                                        </tr>}
                                        <tr>
                                            <td>Cap. social:</td>
                                            <td>{data.companyCapSocial} lei</td>
                                        </tr>
                                    </tbody>
                                </table>
                                {data.companyTVA > 0 &&
                                    <>
                                        <div>
                                            Cot TVA: {data.companyTVA}% - {data.companyTVApay && 'TVA la ncasare'}
                                        </div>
                                    </>
                                }
                            </div>
                            <div className="company-table-inside" style={{ marginLeft: '10px' }}>
                                <div>Client:</div>

                                <table className="table-nomargin table-strong-title">
                                    <tbody>
                                        <tr>
                                            <td colSpan={2}>{data.clientName}</td>
                                        </tr>
                                        <tr>
                                            <td>CIF:</td>
                                            <td>{data.clientCIF}</td>
                                        </tr>
                                        <tr>
                                            <td>Reg. com.:</td>
                                            <td>{data.clientRegCom}</td>
                                        </tr>
                                        <tr>
                                            <td>Adres:</td>
                                            <td>{data.clientAdresa}</td>
                                        </tr>
                                        {data.clientCont && <tr>
                                            <td>Cont:</td>
                                            <td>{data.clientCont}</td>
                                        </tr>}
                                        {data.clientBank && <tr>
                                            <td>Banc:</td>
                                            <td>{data.clientBank}</td>
                                        </tr>}
                                    </tbody>
                                </table>
                            </div>
                        </div>

                        <table className={border ? "table-item-preview table-invoice-services" : 
                            "table-item-preview table-invoice-services table-nomargin"}>
                            <tbody>
                                <tr>
                                    <th>Nr</th>
                                    <th>Denumire produs/serviciu</th>
                                    <th>U.M.</th>
                                    <th>Cant.</th>
                                    <th>Pre unitar</th>
                                    <th>Valoare lei</th>
                                    {data.companyTVA > 0 && <th>Valoare TVA</th>}
                                </tr>
                                {data.items.map((item, i) => {
                                    return (
                                        <tr key={i}>
                                            <td>{i + 1}</td>
                                            <td>{item.name}</td>
                                            <td>{item.um}</td>
                                            <td>{frmt.format(parseFloat(item.qt))}</td>
                                            <td>{frmt.format(parseFloat(item.unitPrice))}</td>
                                            <td>{frmt.format(parseFloat(item.val))}</td>
                                            {data.companyTVA > 0 && <td>{frmt.format(parseFloat(item.tva))}</td>}
                                        </tr>
                                    )
                                })}
                                <tr>
                                    <td colSpan={5} rowSpan={2} style={{textAlign:'right'}}>TOTAL:</td>
                                    {data.companyTVA > 0 && <>
                                        <td>{getPartial(false)} lei</td>
                                        <td>{getPartial(true)} lei</td></>}
                                </tr>
                                <tr>
                                    <td colSpan={data.companyTVA > 0 ? 2 : 1}>{getTotal()} lei</td>
                                </tr>
                            </tbody>
                        </table>
                    </div>
                </div>
            </div>
        )
    }

    const templates = [
        tp1(),
        tp2()
    ]

    const changeTemplate = () => {
        return currentTemplate === templates.length - 1 
        ? setCurrentTemplate(0) : setCurrentTemplate(currentTemplate + 1)
    }

    return (
        <>
            {templates[currentTemplate]}

            <div className="invoice-menu" style={{ cursor: 'pointer', left: '120px' }}
                onClick={() => changeTemplate()}
                title="change template">
                
            </div>
        </>
    )
})